@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@500&display=swap);
*{padding:0;margin:0;border:0;box-sizing:border-box;font-family:"Baloo Paaji 2", monospace}body{max-width:800px;margin:0 auto;padding:0 1rem;background:#18191a;color:whitesmoke}body header{display:flex;align-items:center;justify-content:space-between;margin-top:1rem;margin-bottom:1.5rem}body header .user{display:flex;align-items:center}body header .user .user-img{margin-right:1.5rem;border-radius:50%;display:flex;justify-content:center;align-items:center;border:2px solid whitesmoke}body header .user .user-img img{border-radius:50%;width:3.5rem;height:3.5rem}body header .user .user-text{line-height:1;font-size:1.5rem}body header .notifications{font-size:1.5rem;display:flex;justify-content:center;align-items:center}body .general-balance{background-color:#2e2e2e;border-radius:0.3rem;padding:1rem;display:flex;justify-content:space-between}body .general-balance .amount{display:flex}body .general-balance .amount .line{background-color:#2fff5c;width:0.3rem;border-radius:0.2rem;margin-right:0.5rem}body .general-balance .amount .amount-content{line-height:1.4}body .general-balance .amount .amount-content :last-child{font-weight:bold;letter-spacing:0.1rem}body .general-balance .show-amount{display:flex;justify-content:center;align-items:center;font-size:1.3rem}

